<template>
  <v-container>
    <v-row>
      <v-dialog
        v-model="show"
        scrollable
        :persistent="dialog.persistent ? dialog.persistent : true"
        :fullscreen="dialog.fullScreen"
        :max-width="dialog.maxWidth ? dialog.maxWidth : '600px'"
        :retain-focus="false"
      >
        <v-card class="crud-card">
          <v-card-title>
            {{ heading }}
          </v-card-title>
          <v-card-text>
            <v-form
              id="itemForm"
              ref="form"
              v-model="valid"
              enctype="multipart/form-data"
              @submit.prevent="saveDialog"
            >
              <slot name="form"></slot>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              :color="
                dialog.cancelButtonColor ? dialog.cancelButtonColor : 'error'
              "
              @click="closeDialog"
              >{{
                dialog.cancelButtonText
                  ? dialog.cancelButtonText
                  : $t('crud.addEditDialog.closeButton.text')
              }}</v-btn
            >
            <v-btn
              type="submit"
              :color="addEditButtonColor"
              :loading="edit ? loading.edit : loading.add"
              @click="saveDialog"
              >{{ addEditButtonText }}</v-btn
            >
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </v-container>
</template>

<script>
export default {
  props: {
    show: { type: Boolean, default: false },
    edit: { type: Boolean, default: false },
    loading: { type: Object, required: true },
    dialog: { type: [Object, Boolean], default: () => {} }
  },
  data() {
    return {
      valid: false
    }
  },
  computed: {
    heading() {
      if (this.edit) {
        if (this.dialog.editHeading) {
          return this.dialog.editHeading
        } else {
          return this.$t('crud.addEditDialog.editHeading')
        }
      } else if (this.dialog.addHeading) {
        return this.dialog.addHeading
      } else {
        return this.$t('crud.addEditDialog.addHeading')
      }
    },
    addEditButtonText() {
      if (this.edit) {
        if (this.dialog.editButtonText) {
          return this.dialog.editButtonText
        } else {
          return this.$t('crud.addEditDialog.editButton.text')
        }
      } else if (this.dialog.addButtonText) {
        return this.dialog.addButtonText
      } else {
        return this.$t('crud.addEditDialog.addButton.text')
      }
    },
    addEditButtonColor() {
      if (this.edit) {
        if (this.dialog.editButtonColor) {
          return this.dialog.editButtonColor
        } else {
          return 'success'
        }
      } else if (this.dialog.addButtonColor) {
        return this.dialog.addButtonColor
      } else {
        return 'success'
      }
    }
  },
  watch: {
    show(to) {
      if (to && this.$refs.form) {
        this.$refs.form.resetValidation()
      }
    }
  },
  methods: {
    // close dialog
    closeDialog() {
      this.$refs.form.resetValidation()
      // this.$refs.form.reset()
      this.$emit('close')
    },

    // save or submit dialog
    saveDialog() {
      this.$refs.form.validate()
      if (this.valid) {
        if (this.edit) {
          this.$emit('editItem', this.$refs.form)
        } else {
          this.$emit(
            'saveItem',
            this.$refs.form,
            document.getElementById('itemForm')
          )
        }
      }
    }
  }
}
</script>
<style lang="scss">
.crud-card {
  overflow-x: hidden !important;
}
</style>
